import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Restricted from './views/Restricted.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      redirect: {
          name: "home"
      }
    },
    {
      path: '/home',
      name: 'home',
      component: Home
    },
    {
      path: '/posts',
      name: 'posts',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ './views/Posts.vue')
    },    
    {
      path: '/login',
      name: 'login',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ './views/Login.vue')
    },
    {
      path: '/restricted',
      name: "restricted",
      component: Restricted,
      children: [
        {
          path: 'blogpost',
          name: 'blogpost',                 
          // route level code-splitting
          // this generates a separate chunk (about.[hash].js) for this route
          // which is lazy-loaded when the route is visited.
          component: () => import(/* webpackChunkName: "about" */ './views/Blogpost.vue')
        },
        {
          path: 'deletepost',
          name: 'deletepost',                 
          // route level code-splitting
          // this generates a separate chunk (about.[hash].js) for this route
          // which is lazy-loaded when the route is visited.
          component: () => import(/* webpackChunkName: "about" */ './views/Deletepost.vue')
        }                                     
      ],
    }     
  ]
})
