<template>
  <v-app>
    <router-view @authenticated="setAuthenticated" />
  </v-app>
</template>

<script>

export default {
  name: 'App',
  data () {
    return {
      authenticated: false      
      //
    }
  },
  mounted: function(){
    if(!this.authenticated) {
        this.$router.replace({ name: "home" });
    } 
  },
  methods: {
    setAuthenticated: function (status) {
        this.authenticated = status;
    },
    logout: function () {
        this.authenticated = false;
    }      
  }      
}
</script>
